function Paperella() {
  let sound = new Audio(duck_sound);
  function playSound() {
    sound.play();
    const timer = setTimeout(() => {
      window.open(
        "https://www.eventbrite.it/e/biglietti-air-chamber-pool-party-166899028487"
      );
      clearTimeout(timer);
    }, 500);
  }
  return (
    <div className="Paperella">
      <a onClick={() => playSound()} className="PaperellaLink">
        FAI CLICK QUI PER UN BIGLIETTO
      </a>
    </div>
  );
}

